@use '../utils' as *;
* {
    margin: 0;
    padding: 0;
}

body {
    @include flex-center;
    background-color: var(--tertiary-color);
}

li {
    list-style-type: none;

}

section {
    background-color: var(--primary-color);
}

main {
    width: 60%;
    min-width: 72rem;
}

@include breakpoint(1153px) {
    main {
        width: 100%;
        min-width: auto;
    }
}

@include breakpoint(1000px) {
    .card__section {
        height: auto;
    
        .card {
            &:nth-child(3) {
                display: none;
            }

            width: 40vw;
        }
    
    }
}